import React from 'react'
import './Item.css'
// import Images from './Assets/Savi-logo.png';
// import SubItems from './SubItems'

export default function Item(props) {
    
    return (
        
        <div className="item" onClick={props.handleList}> 
          
              <div className="inner"> 
          
               <p><a href="#card-list-id"> {props.name}</a> </p> 
               

            </div>
            {/* <img className="item-image" src={Images} alt="logo" visibility="hidden"></img>  */}
            
        </div>
    )
}