import React from 'react';
import { makeStyles} from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

let imagePath =''
let imageRequired =''

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    margin: '18px',
    flexDirection: 'column',
    placeItems: 'center',
    width: 200,
    // width: 'minmax(200px, 60%)',
    // boxSizing: 'border-box',
    // height:'25%',
    borderRadius: '15px',
    // boxShadow: '2px 2px 3px 2px gray',
    boxShadow: '3px 3px 3px 2px gray',
    background: 'white',
    zIndex:3,
    flex: '0 2 0.8',
    boxSizing: 'border-box',
    
    // flex: 1,
    // background: 'lightyellow',
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
    // width: 200,
    textAlign:'center',
    // background: "lightyellow",
    // background: "linear-gradient(45deg, yellow, orange, coral)",
    // backgroundImage: "radial-gradient(circle at center center, rgb(241, 195, 19),rgb(244, 150, 32),rgb(246, 105, 45),rgb(249, 59, 57),rgb(251, 14, 70))",
    // backgroundImage: "radial-gradient(circle at 40% 91%, rgba(251, 251, 251,0.04) 0%, rgba(251, 251, 251,0.04) 50%,rgba(229, 229, 229,0.04) 50%, rgba(229, 229, 229,0.04) 100%),radial-gradient(circle at 66% 97%, rgba(36, 36, 36,0.04) 0%, rgba(36, 36, 36,0.04) 50%,rgba(46, 46, 46,0.04) 50%, rgba(46, 46, 46,0.04) 100%),radial-gradient(circle at 86% 7%, rgba(40, 40, 40,0.04) 0%, rgba(40, 40, 40,0.04) 50%,rgba(200, 200, 200,0.04) 50%, rgba(200, 200, 200,0.04) 100%),radial-gradient(circle at 15% 16%, rgba(99, 99, 99,0.04) 0%, rgba(99, 99, 99,0.04) 50%,rgba(45, 45, 45,0.04) 50%, rgba(45, 45, 45,0.04) 100%),radial-gradient(circle at 75% 99%, rgba(243, 243, 243,0.04) 0%, rgba(243, 243, 243,0.04) 50%,rgba(37, 37, 37,0.04) 50%, rgba(37, 37, 37,0.04) 100%),linear-gradient(90deg, rgb(34, 222, 237),rgb(135, 89, 215))",
    backgroundImage: "repeating-linear-gradient(135deg, rgba(0,0,0,0.04) 0px, rgba(0,0,0,0.04) 2px,transparent 2px, transparent 4px),linear-gradient(135deg, rgb(252, 247, 9),rgb(229, 130, 102))",
  },
  
  content: {
    flex: 1,
  },
  
  cover: {
    width: 52,
    height:236,
    alignContent:'center',
    justifyContent:'center',
    marginTop:'3%',
    marginBottom:'3%',
      

  },

  
}));

export default function MediaControlCard(props) {
  const classes = useStyles();

  imageRequired=props.image
  imagePath=process.env.PUBLIC_URL + imageRequired
  
  // let titleofProduct= props.title + '- by Savi Parfume'
  let titleofProduct= props.title + ' - ' + props.category

  return (
    <div classname="card-item">
    
    <Card className={classes.root} raised justify="space-between">
      
      <CardMedia
        className={classes.cover}
        image={imagePath}
        title={titleofProduct}
      />
      <div className={classes.details}>
        <CardContent className={classes.content}>
          {/* <Typography component="h5" variant="h5"> */}
          <Typography component="h6" variant="h6">
            {props.title}
          </Typography>
          {/* <Typography variant="caption" color="textSecondary" display="block" paragraph>
            by Savi Parfume
          </Typography> */}
          <Typography variant="caption" color="textPrimary" >
            Pkg: {props.volume}
          </Typography>
          <Typography>   </Typography>
          <Typography variant="caption" color="textPrimary" paragraph>
            MRP : {props.mrp}*
          </Typography>
          <Typography variant="caption" color="textSecondary"  > *GST extra </Typography>
        </CardContent>
       
      </div>
     
    </Card>
    </div>
  );
}
