import React from 'react'
import MediaCard from './Products/Mediacard'

let imagePath ={}

export default function CardsList(props) {

    return (

        <div>
            <h1 id="card-list-id"> {props.ItemCategory} </h1> 
            <p className="description"> {props.briefDescription} </p> 
            <div className="card-list" >
            
                {
                                            
                props.itemsofList.map((item,index) => <MediaCard  key={index} 
                                                                 title={props.itemsofList[index].title} 
                                                                 category={props.ItemCategory}
                                                                 image={props.itemsofList[index].Image}
                                                                 mrp={props.itemsofList[index].MRP} 
                                                                volume={props.itemsofList[index].Volume} />)}
            </div>
        </div>
    )
}
