import React from 'react'
import './ItemList.css'
import Item from './Item'

export default function Itemlist(props) {

    
  
    return (
        <div>
            <div className="itemList" id="itemsL">
                {props.listOfCatalogue.map( (item,index) => 
                    <Item  name={item} 
                            key={index} 
                     handleList={props.itemList.bind(this,index)}
                           link={props.link}/>)}
            </div>
        </div>
    )
}

